<template>
  <div class="phone-body">
    <transition name="fade" mode="out-in">
      <div class="feed" v-if="step===1" v-dragscroll>
        <!-- step 1 -->
        <app-post v-for="(post,index) in posts" :key="index" :post="post"></app-post>
      </div>
      <!-- step 2 -->
      <!-- v-dragscrollæ”¾åˆ°çˆ¶å®¹å™¨ä¸Š -->
      <div v-else-if="step===2">
        <div
          class="selected-image"
          :style="{backgroundImage:'url('+image+')'}"
          :class="selectedFilter"
        ></div>
        <div class="filter-container" v-dragscroll>
          <app-filter
            v-for="(filter,index) in filters"
            :key="index"
            :image="image"
            :filter="filter"
          ></app-filter>
        </div>
      </div>
      <!-- step 3 -->
      <div v-else-if="step===3">
        <div
          class="selected-image"
          :style="{backgroundImage:'url('+image+')'}"
          :class="selectedFilter"
        ></div>
        <div class="caption-container">
          <textarea placeholder="Write a caption..." @input="inputText" :value="value"></textarea>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import appPost from "./PhoneBodyPost";
import appFilter from "./FilterType";
export default {
  name: "appBody",
  props: {
    posts: Array,
    filters: Array,
    image: String,
    step: Number,
    selectedFilter: String,
    value: String
  },
  components: {
    appPost,
    appFilter
  },
  methods: {
    inputText(e) {
      this.$emit("input", e.target.value);
    }
  }
};
</script>
<style lang="scss" src="../styles/phone-body.scss">
</style>
<style lang="scss" scoped>
@keyframes fade-in {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
@keyframes fade-out {
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
}
.fade-enter-active{
    animation: fade-in 1s ease;
}
.fade-leave-active{
    animation: fade-out 1s ease;
}
</style>




